import React, {useState} from 'react'
import {NavLink} from "react-router-dom";
import {PATH} from "./Routes";
import h from "./Header.module.css"

function Header() {
    const classesBtn = `${h.custom_btn} ${h.btnStyle}`
const [showmenu, setShowMenu] = useState(true)
    return (
        <div>
            <button className={classesBtn} onClick={() => {setShowMenu(!showmenu)}}>
                <span>Click!</span><span>Menu</span>
            </button>
            {
                !showmenu && <nav>
                    <ul className={h.menu}>
                        <li><NavLink to={PATH.PRE_JUNIOR}>PreJunior</NavLink></li>
                        <li><NavLink to={PATH.JUNIOR}>Junior</NavLink></li>
                        <li><NavLink to={PATH.JUNIOR_PLUS}>JuniorPlus</NavLink></li>
                    </ul>
                </nav>
            }

        </div>
    )
}

export default Header
